export default class Module {

  constructor(element) {

    this.setup(element)

  }

  initialize() { }

  setupListeners() { }

  setup(element) {

    this.el = element
    this.identifier = element.dataset.module
    this.data = element.dataset
    this.els = {}

    this.bindMethods(this.defaultMethods)
    this.bindMethods(this.methods)

    this.setupEls()
  }

  bindMethods(methodNames) {
    methodNames.forEach(method => {
      this[method] = this[method].bind(this)
    })
  }

  setupEls() {

    const els = this.el.querySelectorAll(`[data-el^="${this.identifier}"]`)
    const el = els.length ? els[0] : null

    const singleAlias = el.dataset.el.replace(`${this.identifier}.`, "") + "El"
    const pluralAlias = `${singleAlias}s`

    this.els[singleAlias] = el
    this.els[pluralAlias] = []

    els.forEach(el => {
      this.els[pluralAlias].push(el)
    })

  }

  get defaultMethods() {
    return [
      'initialize',
      'setup',
      'setupEls',
      'setupListeners'
    ]
  }

  get methods() {
    return []
  }

}